import React, {Component} from 'react';
import {Link} from "react-router-dom";
import { Breadcrumb, BreadcrumbItem,
            Button, Form, FormGroup, Label, Input, Col, FormFeedback } from 'reactstrap';

class Contact extends Component {
  
    constructor(props) {
        super(props);

        this.state = {
            firstname: '',
            lastname: '',
            telnum: '',
            email: '',
            agree: false,
            contactType: 'Tel.',
            message: '',
            touched:{
                firstname: false,
                lastname: false,
                telnum: false,
                email: false
            }
        };

        this.handleInputChange = this.handleInputChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        
    }

    handleInputChange(event) {
        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;
    
        this.setState({
          [name]: value
        });
    }

    handleSubmit(event) {
        
        alert('Your Feedback is submitted ' );
        
        event.preventDefault();
    }

    handleBlur = (field) => (evt) => {
        this.setState({
            touched: {...this.state.touched, [field]: true }
        })
    }

    validate (firstname, lastname, telnum, email){
        const errors = {
            firstname: '',
            lastname: '',
            telnum: '',
            email: ''
        }

        if(this.state.touched.firstname && firstname.length < 3)
         errors.firstname = "First name should be greater than and equal to 3";
         else if(this.state.touched.firstname && firstname.length > 10)
         errors.firstname = "First name should be less than and equal to 10";

         if(this.state.touched.lastname && lastname.length < 3)
         errors.lastname = "Last name should be greater than and equal to 3";
         else if(this.state.touched.lastname && lastname.length > 10)
         errors.lastname = "Last name should be less than and equal to 10";

         const reg = /^\d+$/   ;

         if(this.state.touched.telnum && !reg.test(telnum))
         errors.telnum = "Tel. number should contain only numbers";
         
         if(this.state.touched.email && email.split('').filter(x => x === '@').length !== 1 )
         errors.email = "Email is not valid";

         return errors;
    }

                render() {
                  
                    const errors = this.validate(this.state.firstname, this.state.lastname, this.state.telnum, this.state.email)

                  return(
                    <div className="home">
                    <img src="assets/images/theme.jpg" alt='23' className='theme'/>
                <div className="centered text-center text-white">
                               <h1><b>Contact Us</b></h1>
                               <Breadcrumb style={{backgroundColor: '#F8F8F8'}}>
                                   <BreadcrumbItem><Link to="/home">Home</Link></BreadcrumbItem>
                                   <BreadcrumbItem active>Contact Us</BreadcrumbItem>
                               </Breadcrumb>
                               </div>
                               <div className="row row-content">
                  
                    <div className="col-12 col-md-9 mt-5 container" style={{backgroundColor: "#F3F3F3"}}>
                    <h3>Send us your Feedback</h3>
                        <Form onSubmit={this.handleSubmit} id ="form">
                        <FormGroup row>
                                <Label htmlFor="firstname" md={2}>First Name</Label>
                                <Col md={6}>
                                    <Input type="text" id="firstname" name="firstname"
                                        placeholder="First Name"
                                        value={this.state.firstname}
                                        valid={this.state.touched.firstname && errors.firstname === ''}
                                        invalid={errors.firstname !== ''}
                                        onBlur={this.handleBlur('firstname')}
                                        onChange={this.handleInputChange} />
                                    <FormFeedback>{errors.firstname}</FormFeedback>
                                </Col>
                            </FormGroup>
                            <FormGroup row>
                                <Label htmlFor="lastname" md={2}>Last Name</Label>
                                <Col md={6}>
                                    <Input type="text" id="lastname" name="lastname"
                                        placeholder="Last Name"
                                        value={this.state.lastname}
                                        valid={this.state.touched.lastname && errors.lastname === ''}
                                        invalid={errors.lastname !== ''}
                                        onBlur={this.handleBlur('lastname')}
                                        onChange={this.handleInputChange} />
                                    <FormFeedback>{errors.lastname}</FormFeedback>
                                </Col>
                            </FormGroup>
                            <FormGroup row>
                                <Label htmlFor="telnum" md={2}>Contact Tel.</Label>
                                <Col md={6}>
                                    <Input type="tel" id="telnum" name="telnum"
                                        placeholder="Tel. Number"
                                        value={this.state.telnum}
                                        valid={this.state.touched.telnum && errors.telnum === ''}
                                        invalid={errors.telnum !== ''}
                                        onBlur={this.handleBlur('telnum')}
                                        onChange={this.handleInputChange} />
                                    <FormFeedback>{errors.telnum}</FormFeedback>
                                </Col>
                            </FormGroup>
                            <FormGroup row>
                                <Label htmlFor="email" md={2}>Email</Label>
                                <Col md={6}>
                                    <Input type="email" id="email" name="email"
                                        placeholder="Email"
                                        value={ this.state.email}
                                        valid={this.state.touched.email && errors.email === ''}
                                        invalid={errors.email !== ''}
                                        onBlur={this.handleBlur('email')}
                                        onChange={this.handleInputChange} />
                                    <FormFeedback>{errors.email}</FormFeedback>
                                </Col>
                            </FormGroup>
                            <FormGroup row>
                                <Col md={{size: 6, offset: 2}}>
                                    <FormGroup check>
                                        <Label check>
                                            <Input type="checkbox"
                                                name="agree"
                                                checked={this.state.agree}
                                                onChange={this.handleInputChange} /> {' '}
                                            <strong>May we contact you?</strong>
                                        </Label>
                                    </FormGroup>
                                </Col>
                                <Col md={{size: 3, offset: 1}}>
                                    <Input type="select" name="contactType"
                                            value={this.state.contactType}
                                            onChange={this.handleInputChange}>
                                        <option>Tel.</option>
                                        <option>Email</option>
                                    </Input>
                                </Col>
                            </FormGroup>
                            <FormGroup row>
                                <Label htmlFor="message" md={2}>Your Feedback</Label>
                                <Col md={10}>
                                    <Input type="textarea" id="message" name="message"
                                        rows="12"
                                        value={this.state.message}
                                        onChange={this.handleInputChange}></Input>
                                </Col>
                            </FormGroup>
                            <FormGroup row>
                                <Col md={{size: 10, offset: 2}}>
                                    <Button type="submit" color="primary">
                                        Send Feedback
                                    </Button>
                                </Col>
                            </FormGroup>
                        </Form>
                    </div>
               </div>
                      </div>
                  );
                }
              
              }



export default Contact;
